.container-contact {
    max-width: 700px;
    margin: 100px 30px auto;
    font-size: 25px;

}
.container-contact h1 {
    font-size: 50px;
    margin-bottom: 10px;
}

.container-contact p:nth-child(4) {
    margin-top: 50px;
}
.container-contact li {
    list-style: none;
    cursor: pointer;
}

.container-contact li:hover{
    color: blue;
    
}